@-webkit-keyframes drift {
    from {
        left: 120%;
    }
    to {
        left: -25%;
    }
}
@-moz-keyframes drift {
    from {
        left: 120%;
    }
    to {
        left: -25%;
    }
}
@keyframes drift {
    from {
        left: 120%;
    }
    to {
        left: -25%;
    }
}
@-webkit-keyframes driftBg {
    0% {
        left: 50%;
        opacity: 0
    }
    3% {
        left: 48%;
        opacity: .7
    }
    100% {
        left: -25%;
        opacity: .7;
    }
}
